<template>
  <div class="submit-form mx-auto">
    <p class="headline">Add Book</p>

    <div v-if="!submitted">
      <v-form ref="form" lazy-validation>
        <v-text-field
            v-model="book.title"
            :rules="[(v) => !!v || 'Title is required']"
            label="Title"
            required
        ></v-text-field>
          <v-text-field
              v-model="book.author"
              :rules="[(v) => !!v || 'Author is required']"
              label="Author"
              required
          ></v-text-field>
          <v-text-field
              v-model="book.bookformat"
              :rules="[(v) => !!v || 'Bookformat is required']"
              label="Bookformat"
              required
          ></v-text-field>
          <v-text-field
              v-model="book.img"
              :rules="[(v) => !!v || 'Img Link is required']"
              label="Img Link"
              required
          ></v-text-field>
          <v-text-field
              v-model="genre"
              :rules="[(v) => !!v || 'Genre is required']"
              label="Genre"
              required
          ></v-text-field>
          <v-text-field
              v-model="book.price"
              :rules="[(v) => !!v || 'Price is required']"
              label="Price"
              required
          ></v-text-field>
          <v-text-field
              v-model="book.description"
              :rules="[(v) => !!v || 'Description is required']"
              label="Description"
              required
          ></v-text-field>
        </v-form>

      <v-btn color="danger" class="mt-3" @click="saveBook">Submit</v-btn>
    </div>

      <!-- This part is displayed after a book has been successfully submitted -->
    <div v-else>
      <v-card class="mx-auto">
        <v-card-title>
          Submitted successfully!
        </v-card-title>

        <v-card-subtitle>
          Click the button to add new Book.
        </v-card-subtitle>

        <!-- Button to reset the form for a new book entry -->
        <v-card-actions>
          <v-btn color="danger" @click="newBook">Add</v-btn>
        </v-card-actions>
      </v-card>
    </div>
  </div>
</template>

<script>
import BookDataService from "../services/BookDataService";

export default {
  name: "add-book",
  data() {
    return {
      // Data model for the new book with default values
      book: {
        id: null,
        title: "",
        description: "",
        published: false,
      },
      // Variable to track if the submission has occurred
      submitted: false,
    };
  },
  methods: {
    saveBook() {
      // Prepare the data for submission
      var data = {
        title: this.book.title,
        description: this.book.description,
        author: this.book.author,
        isbn: this.book.isbn,
        bookformat: this.book.bookformat,
        img: this.book.img,
        genre: this.book.genre,
        price: this.book.price,
      };

       // Call the service to create a new book entry, handle the response
      BookDataService.create(data)
        .then((response) => {
          this.book.id = response.data.id;
          console.log(response.data);
          this.submitted = true;
        })
        .catch((e) => {
          console.log(e);
        });
    },

    // Reset the form and data model for a new entry
    newBook() {
      this.submitted = false;
      this.book = {};
    },
  },
};
</script>

<style>
/* Style to define the maximum width of the form */
.submit-form {
  max-width: 300px;
}
</style>